@font-face {
    font-family: "cachildren-kkum.otf";
    src: url("../font/cachildren-kkum.otf");
    font-weight: normal;
    font-style: normal;
}

/* ===================media 1920px============= */
@media (max-width:1920px) {
    .home {
        max-width: 1180px;

        .info {
            display: flex;
            justify-content: center;

            h1 {
                margin-right: 10px;
                transform: translateY(50%);
                /* 값을 미리 주어줬으면 keyframes에서 시작지점을 안써도 가능 */
                animation-duration: 1s;
                animation-name: h1;
                animation-fill-mode: forwards;
                /* 100%멈춤 */
            }

            h1:nth-child(2) {
                animation-delay: .5s;
            }

            h1:nth-child(3) {
                animation-delay: 1s;
            }

            h1:nth-child(4) {
                animation-delay: 1.5s;
            }

            @keyframes h1 {
                100% {
                    transform: translateY(0%);
                }
            }
        }

        p {
            transform: translateY(100%);
            animation-duration: 1s;
            animation-name: h1;
            animation-fill-mode: forwards;
        }

        p {
            animation-delay: 1.5s;
        }

        @keyframes p {

            /* 애니메이션은 @keyframes랑 묶음 */
            100% {
                transform: translateY(0%);
            }
        }

        .back {
            width: 100%;
            background: linear-gradient(360deg, rgba(241, 140, 226, 1) 0%,
                    rgba(111, 80, 172, 1) 46%,
                    rgba(28, 42, 71, 1) 100%);
        }
    }

    main {
        .T-calendar {

            /*  height: 100%; */
            .Wr {
                width: 97%;
                margin: 0 auto;

                .back {
                    width: 100%;
                    background: linear-gradient(360deg, rgba(241, 140, 226, 1) 0%,
                            rgba(111, 80, 172, 1) 46%,
                            rgba(28, 42, 71, 1) 100%);
                }
            }
        }
    }

    /* Write */
    .write {
        padding: 10px;
        position: relative;
        border: 2px solid pink;
        border-radius: 20px;
        display: none;
        transform: translateY(100%);
        animation-duration: 2s;
        animation-name: h1;
        animation-fill-mode: forwards;

        h2 {
            color: white;
        }

        .msg {
            margin: 0;
        }
    }

    form {
        input {
            padding: 10px;
            background-color: rgb(253, 212, 219);
            border: 1px solid pink;
            border-radius: 20px;
            margin: 20px;
            font-size: 1rem;
        }

        .close {
            padding: 10px;
            background-color: rgb(253, 212, 219);
            border: 1px solid pink;
            border-radius: 20px;
            margin: 20px 0;
            font-family: "cachildren-kkum.otf";
            font-size: 1rem;

            &.no-sc {
                display: flex;
            }
        }
    }

    .write.active {
        display: flex;
        flex-direction: column;
        margin-bottom: 50px;
    }

    .msg {
        padding-left: 0;

        li {
            font-size: 1rem;
            border: 1px solid pink;
            border-radius: 20px;
            text-align: left;
            padding: 5px 10px;
            margin-bottom: 7px;

            .box {
                margin-right: 10px;
                text-decoration: line-through;
            }

            code {
                font-size: 1rem;
            }

            button {
                color: black;
                padding: 6px;
                background: none;
                border: 1px solid pink;
                border-radius: 20px;
                margin-left: 20px;
                font-size: 1rem;
                background-color: rgb(253, 212, 219);
            }

            &.active {
                code {
                    text-decoration: line-through;
                    color: crimson;
                }
            }
        }
    }
    .f-diary{
        width: 65%;
    }
    .msg1 {
        padding: 5px;
        white-space: normal;

        li {
            text-align: left;
        }
    }

    form {
        display: flex;

        input {
            margin-right: 15px;
            background-color: rgb(253, 212, 219);
            border: 1px solid pink;
            border-radius: 20px;
            padding: 5px 10px;
            font-size: 1rem;
            color: black;
        }

        input:nth-child(1) {
            margin: 20px 0;
            background-color: rgb(253, 212, 219);
        }

        button {
            color: black;
            padding: 10px;
            background: none;
            border: 1px solid pink;
            border-radius: 20px;
            margin-left: 20px;
            font-size: 1rem;
            background-color: rgb(253, 212, 219);
        }
    }

    .todo {
        position: absolute;
        top: 5%;
        left: 5%;
        background-color: pink;

    }

    .diary {
        width: 95%;
        padding: 10px;
        position: relative;
        border: 2px solid pink;
        border-radius: 20px;
        display: none;

        h2 {
            margin-bottom: 10px;
        }

        transform: translateX(100%);
        animation-duration: 2s;
        animation-name: diary;
        animation-fill-mode: forwards;

        diary {
            animation-delay: 1.5s;
        }

        @keyframes diary {

            /* 애니메이션은 @keyframes랑 묶음 */
            100% {
                transform: translateY(0%);
            }
        }
    }

    .diary.active1 {
        display: flex;
        flex-direction: column;
    }

    /* 전체 */
    .calendars {
        width: 97%;
        font-family: "cachildren-kkum.otf";
        border-radius: 20px 20px 20px 20px;
        border: 2px solid pink;
        text-align: center;
        position: relative;
        margin: 0 auto 20px auto;

        .out {
            font-size: 1rem;
            position: absolute;
            left: 20px;
            top: 15px;
            font-weight: 600;
        }

        .w-diary {
            font-size: 1rem;
            position: absolute;
            right: 20px;
            top: 15px;
            font-weight: 600;
            cursor: pointer;
        }
        &.no-sc{
            display: none;
        }

        &.on {
            width: 97%;

            /* 상단 */
            .react-calendar__navigation {
                margin: 10px auto;

                span {
                    font-size: 24px;
                    font-weight: 600;
                }
            }

            .react-calendar__navigation button {
                font-size: 1.5rem;
                margin-bottom: 10px;
                padding: 0 8px;

                &:not(:last-child) {
                    margin-right: auto;
                }
            }

            /* 월 */
            .react-calendar__month-view {
                padding: 0px 15px;

                abbr {
                    // 텍스트
                    font-size: 1.5rem;
                    font-weight: 500;
                }
            }

            /* 요일 */
            .react-calendar__month-view__weekdays {
                abbr {
                    // 텍스트 부분
                    font-size: 2rem;
                    font-weight: 500;
                }
            }

            /*hover, focus, 선택됐을 시 */
            .react-calendar__tile:enabled:hover,
            .react-calendar__tile:enabled:focus,
            .react-calendar__tile--active {
                background: rgb(12, 12, 12);
                border-radius: 14px;
            }

            /* 현재 날짜  */
            .react-calendar__tile--now {
                background: pink;
                border-radius: 14px;
            }

            /*hover, focus 시 */
            .react-calendar__tile--now:enabled:hover,
            .react-calendar__tile--now:enabled:focus {
                /*  background: blue; */
                border-radius: 14px;
            }
        }

    }

    .no-sc {
        position: relative;

        img {
            width: 100px;
        }

        p {
            font-size: 2rem;
            margin: 0 auto;
        }

        &.on1 {
            display: none;
        }
    }

}

/* ===================media 780px============= */
@media (max-width:780px) {
    .home {
        .login {
            left: 20%;
        }
    }
}

/* ===================media 480px========================= */
@media (max-width:480px) {
    .home {
