                    rgba(111, 80, 172, 1) 46%,
                    rgba(28, 42, 71, 1) 100%);
        }
    }

    main {
        .T-calendar {
            .Wr {
                .back {
                    width: 100%;
                    background: linear-gradient(360deg, rgba(241, 140, 226, 1) 0%,
                            rgba(111, 80, 172, 1) 46%,
                            rgba(28, 42, 71, 1) 100%);
                }
            }
        }
    }

    /* 달력  */
    .calendars {
        width: 97%;

        /* event */
        &.on {
            width: 97%;

            /* 상단 */
            .react-calendar__navigation {
                margin: 10px auto;

                span {
                    font-size: 24px;
                    font-weight: 600;
                }
            }

            .react-calendar__navigation button {
                font-size: 1.5rem;
            }

            /* 월 */
            .react-calendar__month-view {
                padding: 0px 15px;

                abbr {
                    // 텍스트
                    font-size: 1.5rem;
                    font-weight: 500;
                }
            }

            /* 요일 */
            .react-calendar__month-view__weekdays {
                abbr {
                    // 텍스트 부분
                    font-size: 1.5rem;
                    font-weight: 500;
                }
            }

            .no-sc {
                display: none;
            }
        }

        /* event 종료 */

        /* 안내문구 */
        .no-sc {
            position: absolute;
            top: 105%;
            left: 3%;

            img {
                width: 100px;
            }

            p {
                font-size: 2rem;
                font-weight: 500;
            }
        }
    }

    /* 첫년도선택 */
    .react-calendar__century-view__decades {
        button {
            font-weight: 600;
        }
    }

    /* 년도선택*/
    .react-calendar__decade-view__years {
        button {
            font-weight: 600;
        }
    }

    /* 상단 */
    .react-calendar__navigation {
        margin: 0px auto 15px;

        span {
            font-size: 2rem;
            font-weight: 600;
        }
    }

    .react-calendar__navigation button {
        font-size: 2.5rem;
    }

    /* 월 */
    .react-calendar__month-view {
        padding: 0px 15px;

        abbr {
            // 텍스트
            font-size: 1.5rem;
            font-weight: 500;
        }

        .star {
            font-size: 10px;
        }
    }

    /* 요일 */
    .react-calendar__month-view__weekdays {
        abbr {
            // 텍스트 부분
            font-size: 2rem;
            font-weight: 500;
        }
    }

    /* 일 */
    .react-calendar__tile {
        font-weight: 600;
        font-size: 1.5rem;
    }

    /* write */
    .write {
        padding: 10px;

        .msg {
            margin: 0;
        }
    }

    form button {
        padding: 8px;
        background: none;
        border: 1px solid pink;
        border-radius: 20px;
        /* margin-left: 20px; */
        font-size: 10px;
    }
}

